                            <el-menu-item v-for="(item,i) in menuList" :key="i" :index="item.index">
                                    {{ item.title }}
                            </el-menu-item> 

                            <el-submenu>
                                <template slot="title">
                                    <span>专案管理</span>
                                </template>
                                <el-menu-item-group>
                                    <el-menu-item v-for="(items,i) in menuChild" :key="i" :index="items.index">{{ items.title }}</el-menu-item>
                                </el-menu-item-group>
                            </el-submenu>
                            <el-menu-item>
                                <span slot="title" @click="evaluation">测评</span>
                            </el-menu-item>
                    </el-menu>
                </el-aside>
                <el-main><router-view/></el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
export default {
    name: 'home',
    components: {},
    props: {},
    data() {
        return {
            menuList: [
                {
                    index: 'archives',
                    title: '档案管理'
                },
                {
                    index: 'childMedical',
                    title: '儿童体检'
                },
                // {
                //     index: 'evaluation',
                //     title: '测评'
                // },
                {
                    index: 'maintenance',
                    title: '模板维护'
                },
                
                {
                    index: 'basicData',
                    title: '基础数据维护'
                },
            ],
            menuChild: [       
                {
                    index: 'premature',
                    title: '早产儿管理',
                },
                {
                    index: 'obesity',
                    title: '肥胖儿管理',
                },
                {
                    index: 'weakBaby',
                    title: '体弱儿管理',
                },
                {
                    index: 'otherBaby',
                    title: '其他专案管理',
                }, 
            ]
          
          
        };
    },
    computed:({
        onRoutes(val){
            // return this.$route.path.replace('/','');
        }
    }),
    mounted() {
     
    },
    methods: {
        handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      evaluation(){
          window.location.href = 'http://www.baidu.com'; 
        //   window.open("http://www.baidu.com");
      }
      
    }
}
</script>
<style lang="less" scoped>
.main-page{
   
 .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-header{
        background-color: #545c64;
        color: #fff;
        text-align: center;
        line-height: 60px;
        .title {
            float: left;
            width:250px;
            height: 70px;
            line-height: 70px;
            font-size: 25px;
        }
        .addRoll{
            float: right;
            width:250px;
           
        }
  }
  
  .el-select{
      margin-top: 20px;
  }
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
   
  }
  
  .el-main {
    background-color: #fff;
    
  }

  .el-container{
    position: absolute; 
    width: 100%; 
    top: 0px ; 
    left: 0 ; 
    bottom: 0;
    }
    .el-header{
    padding: 0;
    z-index: 1000;
    }
  
    .el-header .el-menu{
    border-bottom: none;
    }
    .el-aside, .el-main{
    padding-top: 60px;
    }
    .el-aside{
    background: #324157;
    }
    .el-aside .el-menu{
    border-right: none;
    }
}
</style>



